import { getState, setState } from '../state';
import { getExistingProfileRequests, saveRequestsAsProfile} from '../services/profile';
import _ from "lodash";
import chalk from "chalk";

export const startCaptureHandler = (request, response) => {
  setState({ requests: [], capturing: true });

  response.status(200).send();
};

export const cancelCaptureHandler = (request, response) => {
  setState({ requests: [], capturing: false });

  response.status(200).send();
};


